'use client'

import {
  UserOutlined,
  ProjectOutlined,
  ExperimentOutlined,
  TrophyOutlined,
  BookOutlined,
  RocketOutlined,
  BulbOutlined,
} from '@ant-design/icons'

const icons = [
  { key: 'About', icon: <UserOutlined /> },
  { key: 'Projects', icon: <ProjectOutlined /> },
  { key: 'Experience', icon: <ExperimentOutlined /> },
  { key: 'Certifications', icon: <TrophyOutlined /> },
  { key: 'Education', icon: <BookOutlined /> },
  { key: 'Ongoing', icon: <RocketOutlined /> },
  { key: 'Thoughts', icon: <BulbOutlined /> },
]

export default function TopIcons({
  active,
  setActive,
}: {
  active: string
  setActive: (tab: string) => void
}) {
  return (
    <div className="flex items-center gap-2 flex-wrap">
      {icons.map(item => {
        const isActive = active === item.key

        return (
          <button
            key={item.key}
            onClick={() => setActive(item.key)}
            className={`
              group
              flex items-center
              h-9
              px-3
              rounded-xl
              transition-all duration-200
              ${
                isActive
                  ? 'bg-green-200/70 text-green-800 shadow-sm'
                  : 'text-slate-500 hover:bg-yellow-100/70 hover:text-slate-800'
              }
            `}
          >
            {/* ICON */}
            <span className="flex items-center justify-center text-sm">
              {item.icon}
            </span>

            {/* LABEL (space reserved → no jump) */}
            <span
              className="
                ml-2
                w-0
                overflow-hidden
                whitespace-nowrap
                text-sm
                font-medium
                opacity-0
                transition-all duration-200
                group-hover:w-[110px]
                group-hover:opacity-100
              "
            >
              {item.key}
            </span>
          </button>
        )
      })}
    </div>
  )
}
